body {
  color: #333;
}

img {
  max-width: 100%;
  height: auto;
}



.sec01{
  margin: 80px 0;
}


h1 {
  font-size: 40px;
  font-weight: 700;
}

.p-sec01__accordion {
  margin-top: 40px;
}

.p-accordion__Q:nth-of-type(n + 2) {
  margin-top: 12px;
}

.p-accordion__QButton {
  display: block;
  width: 100%;
  padding: 16px 8px;
  background: #eee; 
  outline: none;
  transition: all 0.3s ease-in-out;

  &:hover, &:focus {
    color: #eee;
    background: #333;
  }
}

.p-accordion__A {
  display: none;
  padding: 16px 8px;
  border: solid 2px #eee;
}

.p-accordion__ATitle {
  font-weight: 700;
}

.p-accordion__AText {
  margin-top: 4px;
}

.sec02, .sec03, .sec04 {
  width: 100%;
  height: 100vh;
  padding-top: 80px;
}

.sec02 {
  background: #efd;
}
.sec03 {
  background: #fec;
}
.sec04 {
  background: #acd;
}

.c-toTop {
  position: fixed;
  bottom: 40px;
  right: 40px;
  width: 40px;
  height: 40px;
}

.c-toTop__text {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 100%;
  height: 100%;
  color: white;
  font-size: 14px;
  font-weight: 700;
  background: rgb(90, 105, 194);
  border-radius: 8px;
}